import React from 'react'
import Typewriter from 'typewriter-effect';
import $ from 'jquery'
import AnchorLink from 'react-anchor-link-smooth-scroll'
import './Banner.css'



export default function Banner() {


  $(document).ready(function () {
    let textos = [
      "ReactJS",
      "PHP",
      "JavaScript",
      "TypeScript",
      "NodeJS",
      "Express",
      "Restify",
      "TDD",
      "React Native",
      "Jasmine",
      "HTML5",
      "CSS3",
      "GIT",
      "Mysql",
      "PostgreSQL",
      "MongoDB",
      "Docker",
      "Heroku"
    ];

    let atual = 0;
    $('#habilidades').text(textos[atual++]);
    setInterval(() => {
      $('#habilidades').fadeOut(() => {
        if (atual >= textos.length) atual = 0;
        $('#habilidades').text(textos[atual++]).fadeIn();
      });
    }, 4000);
  });


  return (
    <div className="banner">
      <div className="container content">

        <div className="name">
          <Typewriter
            options={{
              strings: ['Pedro Henrique', 'Desenvolvedor Web', 'Desenvolvedor Mobile', ],
              autoStart: true,
              loop: true,
            }}
          />
        </div>

        <span id="habilidades"></span><br></br>
        <AnchorLink href="#hello"><i style={{ marginTop: 100, opacity: 0.8 }} className="fa fa-5x fa-angle-down"></i></AnchorLink>
      </div>
    </div>
  )
}